async function dropDownBanners() {
	let bannersArr = document.querySelectorAll('.big-container .banner__main-block')
	document.addEventListener('scroll', (e) => {
		if (scrollY > 150) {
			bannersArr.forEach(element => {
				element.classList.add('_active')
			})
		} else {
			bannersArr.forEach(element => {
				element.classList.remove('_active')
			})
		}
	})
}
dropDownBanners()
